.header{border-bottom:2px solid rgba(249,249,250,.4);display:flex;flex-direction:column;grid-column:1/-1;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.header h1{margin-bottom:0}.header p{font-size:1.2rem}.header a{color:#fff;text-decoration:none}.header .breadcrumb{align-items:flex-end;display:flex;flex:1 1}.header h2{align-items:center;display:flex}.header h2 span{font-size:24px;font-weight:400}.header .home{background:url(data:image/svg+xml;base64,) no-repeat;background-size:24px auto;height:24px;transition:-webkit-transform 75ms;transition:transform 75ms;transition:transform 75ms, -webkit-transform 75ms;width:24px}.header .home:focus,.header .home:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}.header .home:active{-webkit-transform:scale(1.2);transform:scale(1.2)}